#context-menu {
    position: fixed;
    z-index: 10000;
    width: 150px;
    background: #1b1a1a;
    border-radius: 5px;
    transform-origin: top left;
    display: none;
}
  
#context-menu .item {
    padding: 8px 10px;
    font-size: 15px;
    color: #eee;
    cursor: pointer;
    border-radius: inherit;
}
  
#context-menu .item:hover {
    background: #343434;
}

#context-menu .visible {
    transform: scale(1);
    transition: transform 200ms ease-in-out;
}